* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Hiragino Kaku Gothic ProN", sans-serif;
}

a {
  text-decoration: none;
}

.clear {
  clear: left;
}

.container {
  max-width: 1170px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.top-title {
  padding: 10px 0 0px 0;
  background-size: cover;
  color: rgba(9, 10, 10, 0.46);
  text-align: center;
  margin-top: 65px;
  margin-bottom: 20px;
  border-bottom: 2px solid skyblue;
  
}

.top-wrapper {
  padding: 10px 0 0 0;
  /*background-image: url(3.jpg);*/
  background-size: cover;
  color: rgb(110, 79, 79);
  text-align: center;
  position: relative;
}

.top-wrapper img {
  width: 100%;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
}

.top-wrapper h2 {
  opacity: 0.7;
  font-size: 45px;
  letter-spacing: 5px;
  margin-top: 0px;
}

.top-wrapper h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  color: white;
  opacity: 0.7;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
}

.top-wrapper p {
  opacity: 0.7;
}

.btn-wrapper {
  text-align: center;
  margin: 20px 0;
}

.btn-wrapper p {
  margin: 10px 0;
}

.signup {
  background-color: #239b76;
}

.facebook {
  background-color: #3b5998;
  margin-right: 10px;
}

.twitter {
  background-color: #55acee;
}

.btn {
  padding: 8px 24px;
  color: white;
  display: inline-block;
  opacity: 0.8;
  border-radius: 4px;
  text-align: center;
}

.btn:hover {
  opacity: 1;
}

.fa {
  margin-right: 5px;
}

.header {
  height: 65px;
  width: 100%;
  background-color: white ;
  position :fixed;
  top: 0;
  z-index: 10;
  border-bottom: 2px solid rgba(255, 153, 0, 0.76);
}

.logo {
  width: 200px;
  margin-top: 0px;
}

.header-left {
  float: left;
}

.header-right {
  float: right;
  margin-right: -50px;
}

.header-right a {
  line-height: 65px;
  padding: 0 25px;
  color: black;
  display: block;
  float: left;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  font-size: 14px;
  text-decoration: none;
  text-align: center;
}

.header-right a:hover {
  background-color: rgba(135, 206, 235, 0.9);
}

.router-link-active {
  background-color: skyblue;
}

/* menu-iconのCSSを貼り付けてください */

.menu-icon {
  color: white;
  float: right;
  font-size: 25px;
  padding: 21px 0;
  /* displayをnoneに指定してください */
  display: none;
}

.lesson-wrapper {
  padding-bottom: 80px;
  padding-left: 5%;
  padding-right: 5%;
  background-color: #f7f7f7;
  text-align: center;
}

.heading {
  padding-top: 20px;
  padding-bottom: 20px;
  color: #5f5d60;
  border-bottom: 200px;
}

.heading h2 {

  font-size: 16px;

}

.lesson {
  float: left;
  width: 33.33%;
}

.lesson p{
  font-size: 18px;
  color: black;
}

.lesson-icon {
  position: relative;
  margin: 10px;
}

.lesson-icon img {
  width: 100%;
  height: 100%;
}

.lesson-icon:hover{
  -webkit-box-shadow: 10px 10px 10px rgba(0,0,0,0.5);
          box-shadow: 10px 10px 10px rgba(0,0,0,0.5);
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
}

.lesson-icon p {
  position: absolute;
  top: 70%;
  width: 100%;
  font-size: 20px;
  font-weight: 400;
  color: white;
}

.lesson li{
  text-align: left;

}

.text-contents {
  width: 80%;
  display: inline-block;
  margin-top: 15px;
  font-size: 13px;
  color: #b3aeb5;
}

.heading h3 {
  font-weight: normal;
}

.message-wrapper {
  border-top: 2px solid skyblue;
  padding-bottom: 70px;
  text-align: center;
  background-color: white;
}

.message-wrapper h2, h3{
  color: grey;
}

.message {
  padding: 15px 40px;
  background-color: #5dca88;
  cursor: pointer;
  -webkit-box-shadow: 0 7px #1a7940;
          box-shadow: 0 7px #1a7940;
}

.message:active {
  position: relative;
  top: 7px;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.footer img {
  width: 125px;
}

.footer p {
  color: #b3aeb5;
  font-size: 14px;
}

.footer {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgb(214, 207, 207);
}

.img-wrap {
  overflow: hidden;
  position: relative;
}

.img-wrap:before {
  -webkit-animation: img-wrap 2s cubic-bezier(.4, 0, .2, 1) forwards;
          animation: img-wrap 2s cubic-bezier(.4, 0, .2, 1) forwards;
  background: #fff;
  bottom: 0;
  content: '';
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}

@-webkit-keyframes img-wrap {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes img-wrap {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

.news-left p, li{
  text-align: left;
}

.news-right {

}

.news {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;

}

.news-detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.news-detail p{
  font-size: 18px;
  color: black;
  text-align: left;
  border-bottom: 1px solid grey;
  margin-left: 10px;
}

.date {
  text-align: left;
}/* タブレット向けレイアウト */
@media all and (max-width: 1000px) {
    .lesson {
      width: 50%;
      margin-bottom: 50px;
    }
  
    footer {
      text-align: center;
    }
  
    .top-wrapper h1 {
      font-size: 32px;
    }
  
    .heading h2 {
      font-size: 20px;
    }

    .news {
        width: 50%;
        margin-bottom: 50px;
      }
  }
/* スマホ向けレイアウト */
@media all and (max-width: 670px) {
    .lesson {
      width: 100%;
    }

    .news {
        width: 100%;
      }
  
      .news-detail {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        width: 100%;
      }

    .btn {
      width: 100%;
    }
  
    .facebook {
      margin-bottom: 10px;
    }
    
    .top-wrapper {
      text-align: left;
    }
  
    /* header-rightクラスのdisplayをnoneに指定してください */
    .header-right {
      display: none;
    }
    
    /* menu-iconクラスのdisplayをblockに指定してください  */
    .menu-icon {
      display: block;
    }
    
    .top-wrapper h1 {
      font-size: 24px;
    }
  
    .top-wrapper p {
      font-size: 14px;
    }

    .lesson-wrapper h2 {
        text-align: left
    }

    .img-wrap h1 {
        font-size: 14px;
    }


  }